.page-wrapper .main-page .main-page-inner.canvas {padding: 0;    width: 100%; height: 100%;}
.page-wrapper .main-page .main-page-inner.canvas.active{z-index: 2;}

.container { flex: 1; height: 100%; background-color: grey; }

.tool-bar {background-color: #FFFFFFDD; height: 100%; border-right: 1px solid #00000088;}
.canvas .setting {position: absolute; top: 0; left: 0px; width: 300px; background-color: #FFFFFFDD; height: 100%; border-left: 1px solid #00000088; transition: all 0.3s; transform: translateX(-100%);}
.canvas .setting.show { transform: translateX(0);}
.canvas .setting * {color:black;}
.canvas .setting-wrapper {position: relative; width: 100%; padding: 20px 0px; height: calc(100vh - 160px);}
.canvas .setting .setting-arrow { position: absolute; top: 0; right: -30px; width: 30px; height: 60px; transition: all 0.3s; background-color: #FFFFFFDD; cursor: pointer;}
.canvas .setting.show .setting-arrow { right: 0px;}
.canvas .setting-wrapper .main-title {font-size: 24px;}
.canvas .setting-wrapper .arrow {position: absolute; top: 0; left: 0; width: 45px; height: 60px; transition: all 0.3s; background-color: #00000000; cursor: pointer;}
.canvas .setting-wrapper .arrow img {width: 60%; transform: rotate(180deg); transition: all 0.3s;}
.canvas .setting.hide .setting-wrapper .arrow {left: -45px; background-color: #000000DD;}
.canvas .setting.hide .setting-wrapper .arrow img {transform: rotate(0deg);}

.canvas .setting-content {margin-top: 40px; height: calc(100% - 60px); overflow-y: auto;}
.canvas .setting-content .title {margin-top: 10px; text-align: center; font-size: 20px;}
.canvas .setting-content .setting-part {border-top: 1px solid #00000044;}
.canvas .setting-content .setting-part .setting-inner {max-height: 0px; transition: all 0.3s; overflow: hidden;}
.canvas .setting-content .setting-part.active .setting-inner {max-height: 150px;}
.canvas .setting-content .setting-part.object-setting-part.active .setting-inner {max-height: 400px;}
.canvas .setting-content .sub-title {padding: 12px 10px;  font-size: 20px; font-weight: 700; text-align: center; }
.canvas .setting-content .sub-title.flag-title { cursor: pointer; transition: all 0.3s; }
.canvas .setting-content .sub-title.flag-title:hover { background-color: #BBFFEE66; color: #3366FF; }
.canvas .set-item {margin: 10px 20px; display: flex;}
.canvas .set-item .label {width: 90px; font-size: 16px;}
.canvas .set-item .content {flex: 1; margin: 0px 10px;}
.canvas .set-item .content input {flex: 1;}
.canvas .set-item .value {width: 55px; text-align: right; margin-right: 5px;}
.canvas .set-item .value input {width: 100%;}
